@import '_styles/variables';
@import '_styles/mixins';

.ArticleHeader {

  &__title {
    margin-bottom: 0;
  }

  &__divider {
    display: inline-block;
    width: 5em;
    height: 2px;
    background-color: currentColor;
    overflow: hidden;
    line-height: 0;
    margin: 1.5em 0;
  }

  &__subtitle {
    @include fontSubtitle;
    margin-top: 0;
  }

  &__date {
    @include fontCaps;
    margin-bottom: 0;
    font-size: 0.625em;
    opacity: 0.4;
  }

  &__image {
    margin: $blockMargin 0;
  }
}
