@import '_styles/variables';
@import '_styles/mixins';

.SkillModule {

  &__container {
    padding: calc(20px + 2.5vw);
    background: transparent url('../_images/misc/bannerPattern.png') repeat;
    border-radius: 2px;

    @include respondAbove(s) {
      padding: 60px;
    }
  }

  &__icon {
    line-height: 0;
    text-align: center;

    svg {
      fill: currentColor;
    }
  }

  &__title {
    margin-top: 0.75em;
    text-align: center;;
  }

  p:last-child {
    margin-bottom: 0;
  }
}
