@import '_styles/variables';
@import '_styles/mixins';

.LazyImage {
  position: relative;
  width: 100%;
  padding-top: 100%;
  overflow: hidden;
  background-color: #f2f2f4;

   &__preview {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-size: cover;
    background-position: center;
    // filter: blur(40px);
    // -webkit-filter: blur(40px);
  }

  &__image {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-size: cover;
    background-position: center;
    opacity: 0;
    transition: opacity 3s $romiem-easingCurveOut 0.6s;
  }

  &__image--loaded {
    opacity: 1;
  }

  &__image--cached {
    transition-duration: 0s;
    transition-delay: 0s;
  }
}
