html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  @include fontDefault;
  font-size: $fontSize;
  line-height: $lineHeight;
  color: $color__default;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4 {
  margin: 2em 0 1em;
}

h1 {
  @include fontH1;
}

h2 {
  @include fontH2;
}

h3 {
  @include fontH3;
}

h4 {
  @include fontH4;
}

p {
  margin: 1em 0;
}
