//
//
// Anchors - there's a ton of extra styles in this in the event a button needs to be styled like an anchor
//
//

.u-anchor {
  color: currentColor;
  font: inherit;
  text-decoration: none;
  padding: 0;
  border: none;
  outline: none;
  color: inherit;
  background: none;
  line-height: 1;
  cursor: pointer;
  transition: all 0.2s $romiem-easingCurve;

  &:hover {
    opacity: 0.5;
  }

  &--accent {
    color: $color__accent;
  }

  &--accentHover {

    &:hover {
      opacity: 1;
      color: $color__accent;
    }
  }

  &--underline {
    border-bottom: 1px solid currentColor;
  }
}



//
//
// Buttons
//
//

.button {
  display: inline-block;
  padding: 0.5em 1em;
  @include fontCaps;
  text-decoration: none;
  border: 0;
  border-radius: 2px;
  background-color: rgba($color__default, 1);
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s $romiem-easingCurve;

  &:hover {
    opacity: 0.5;
  }
}




//
//
// Animations
//
//

.bulletList {

  > li {
    margin-bottom: 0.25em;
    padding-left: 0.5em;
  }
}



//
//
// Animations
//
//

.a-routeFadeIn {
  animation: a-routeFadeIn 0.75s $romiem-easingCurveOut 0.25s;
  animation-fill-mode: both;
}

@keyframes a-routeFadeIn {
  0%    { opacity: 0; height: 0; overflow: hidden; }
  0.01% { height: auto; overflow: visible; }
  100%  { opacity: 1; }
}

.a-routeFadeIn.fade-exit {
  animation: none;
  animation-fill-mode: none;
  opacity: 1;
  transition: opacity 0.2s $romiem-easingCurveOut;
  transition-delay: 0;
}

.a-routeFadeIn.fade-exit-active {
  opacity: 0;
}


