@import '_styles/variables';
@import '_styles/mixins';

.SiteHeader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 62px;
  text-align: center;
  background: rgba(255,255,255,0.92);
  display: none;

  &--visible {
    display: block;
  }

  &__iconLink {
    color: $color__light;
    padding: 0.5em;
    margin: 0 0.25em;
    line-height: 0;
    transform: scale(1); // Need this to force GPU acceleration on the link (prevent wobbling effect when hovering over SVG link)

    &--active {
      color: $color__title;
    }
  }

  &__logo {
    position: absolute;
    left: 8px;
    top: 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 0;
  }

  &__logoIcon {
    width: 90px;
    fill: currentColor;
  }

  &__icon {
    width: 1.25em;
    fill: currentColor;
    vertical-align: middle;
  }

  &__nav {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 0;
    overflow: hidden;
    opacity: 0;
    z-index: 101;
    background: rgba(255,255,255,0.96);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: opacity 0.2s $romiem-easingCurveOut, height 0s $romiem-easingCurveOut 0.2s;

    &--show {
      height: 100%;
      opacity: 1;
      transition: opacity 0.4s $romiem-easingCurveOut, height 0s $romiem-easingCurveOut;
    }

    @include respondAbove(s) {
      position: static;
      flex-direction: row;
      opacity: 1;
      height: 100%;
      background: none;
    }
  }

  &__navSocial {
    position: absolute;
    right: 52px;
    top: 14px;

    @include respondAbove(s) {
      right: 8px;
    }
  }

  &__link {
    display: inline-block;
    @include fontCaps;
    padding: 1.25em;
    font-size: 1em;
    text-decoration: none;
    vertical-align: middle;
    margin: 0 4px;
    color: $color__light;

    @include respondAbove(s) {
      @include fontCaps;
      padding: 0.75em;
    }

    &--active {
      color: $color__title;
      opacity: 1;
    }
  }

  &__hamburger {
    position: fixed;
    right: 4px;
    top: 8px;
    z-index: 102;
    width: 48px;
    padding: 12px;
    border: 0;
    background: transparent;
    opacity: 0.25;

    @include respondAbove(s) {
      display: none;
    }

    &:after,
    &:before {
      border-radius: 2px;
      content: '';
      display: block;
      background-color: currentColor;
      height: 2px;
      margin: 6px 0;
      transition: transform .4s $romiem-easingCurveOut;
    }

    &--active {

      &:before {
        transform: translateY(4px) rotate(135deg);
      }

      &:after {
        transform: translateY(-4px) rotate(-135deg);
      }
    }
  }
}
