@import '_styles/variables';
@import '_styles/mixins';

.CodeSample {
  width: 100%;
  margin: $blockMargin 0;

  pre {
    font-size: 1em !important;
    border-radius: 2px !important;
    border: none !important;
    line-height: 1.6;
  }

  code {
    white-space: pre-wrap;
    font-family: monospace !important;
  }
}
