$breakpoints: (
  xs:   320px,
  s:    640px,
  m:    960px,
  l:    1280px,
  xl:   1600px,
  xxl:  1920px,
);

$fontFamily__primary:           'Calendas Plus', 'serif';
$fontFamily__secondary:         'Inter var', 'sans-serif';
$fontSize:                      16px;
$lineHeight:                    2;

$color__default:                #515050;
$color__title:                  #171515;
$color__light:                  #aeadad;
$color__accent:                 #6187ea;
$color__anchor:                 $color__accent;

$blockMargin:                   calc(8px + 2.5vw);








$font-family-rubik:             'Rubik', 'sans-serif';
$font-size:                     16px;
$color-default:                 #45454E;

$romiem-easingCurve:            cubic-bezier(.37,0,0,1);
$romiem-easingCurveOut:         cubic-bezier(.05,.4,0,1);
$romiem-easingCurveIn:          cubic-bezier(1,0,.95,.6);
