
@import '_styles/variables';
@import '_styles/mixins';

.ArticleImageGrid {
  margin: $blockMargin 0;

  &__grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 2px;
  }

  &__image {
    grid-column: span 3;
  }

  &__caption {
    @include fontFootnote;
    opacity: 0.625;
    margin-top: 0.5em;
    text-align: center;;
  }
}
