//
//
//  respondAbove
//
//

 @mixin respondAbove($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    $breakpoint-value: map-get($breakpoints, $breakpoint);
    @media (min-width: $breakpoint-value) {
      @content;
    }
  } @else {
    @warn 'Invalid breakpoint: #{$breakpoint}.';
  }
}




//
//
//  fontDefault
//
//

@mixin fontDefault {
  font-family: $fontFamily__primary;
  font-size: $fontSize;
  font-weight: 400;
}

.mxn__fontDefault {
  @include fontDefault;
}




//
//
//  fontCaps
//
//

@mixin fontCaps {
  font-family: $fontFamily__secondary;
  font-size: 0.75rem;
  font-weight: 600;
  font-style: normal;
  letter-spacing: 0.175em;
  text-transform: uppercase;
}

.mxn__fontCaps {
  @include fontCaps;
}




//
//
//  fontSubtitle
//
//

@mixin fontSubtitle {
  font-family: $fontFamily__primary;
  font-size: 1.125rem;
  font-weight: 400;
}

.mxn__fontSubtitle {
  @include fontSubtitle;
}




//
//
// fontLeading
// Range 20-24px (delta 4)
//
//

@mixin fontLeading {
  font-family: $fontFamily__primary;
  font-weight: 400;
  font-size: calc(#{20px / $fontSize}rem + 0.25vw); // vw value = delta * 100 / breakpoint

  @include respondAbove(xl) {
    font-size: #{24px / $fontSize}rem;
  }
}

.mxn__fontLeading {
  @include fontLeading;
}




//
//
//  fontH1, fontH2, fontH3
//
//

// Range 28-36px (delta 8)
@mixin fontH1 {
  font-family: $fontFamily__primary;
  font-weight: 400;
  font-size: calc(#{28px / $fontSize}rem + 0.5vw); // vw value = delta * 100 / breakpoint

  @include respondAbove(xl) {
    font-size: #{36px / $fontSize}rem;
  }
}

// Range 24-32px (delta 8)
@mixin fontH2 {
  font-family: $fontFamily__primary;
  font-weight: 400;
  font-size: calc(#{24px / $fontSize}rem + 0.5vw); // vw value = delta * 100 / breakpoint

  @include respondAbove(xl) {
    font-size: #{32px / $fontSize}rem;
  }
}

// Range 20-24px (delta 4)
@mixin fontH3 {
  font-family: $fontFamily__primary;
  font-weight: 400;
  font-size: calc(#{20px / $fontSize}rem + 0.25vw); // vw value = delta * 100 / breakpoint

  @include respondAbove(xl) {
    font-size: #{24px / $fontSize}rem;
  }
}

// Fixed 18px
@mixin fontH4 {
  font-family: $fontFamily__primary;
  font-weight: 400;
  font-size: 1.125rem;
}




//
//
//  fontFootnote
//
//

@mixin fontFootnote {
  font-family: $fontFamily__primary;
  font-weight: 400;
  font-size: 0.875rem;
}

.mxn__fontFootnote {
  @include fontFootnote();
}
