@import '_styles/variables';
@import '_styles/mixins';

.Card {

  &__image {
    display: block;
  }

  &__title {
    margin: 0.875em 0 0;
    line-height: 1.75em;
  }

  &__description {
    margin: 0.375em 0;
    line-height: 1.75em;
  }

  &__timeAgo {
    @include fontCaps;
    font-size: 0.625em;
    opacity: 0.3;
    margin: 0;
  }
}
